import React from "react";

const AddButton = (props) => {
    return (<button type="button" className="btn btn-info" data-toggle="modal" data-target="#addModal">Add</button>);
}

const EditButton = (props) => {
    return (<button type="button" className="btn btn-info" onClick={props.selectRowAndPreFill} data-toggle="modal" data-target="#editModal">Edit</button>);
}

const DeleteButton = (props) => {
    return (<button type="button" className="btn btn-warning" onClick={props.selectRow} data-toggle="modal" data-target="#deleteModal">Delete</button>);
}

const BrowseButton = (props) => {
    return (<button type="button" className="btn btn-info">Browse</button>);
}

const SearchButton = (props) => {
    return (<button type="button" className="btn btn-info">Search</button>);
}

const Button = (props) => {
    return (<button type="button" className="btn btn-info">{props.text}</button>);
}

export {AddButton, SearchButton, EditButton, DeleteButton, BrowseButton, Button};


